import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ClosingFifoReportComponent } from './closing-fifo-report.component';

const routes: Routes = [
  {
    path: '',
    component: ClosingFifoReportComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ClosingFifoReportRoutingModule { }



