import { Component, OnInit, ViewChild, ViewContainerRef, ComponentFactoryResolver, Inject } from '@angular/core';
import { QuickaccessComponent, CurrentUserService, AllReportService, FileExportService } from '../../../shared';
import { jqxComboBoxComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxcombobox';
import { jqxLoaderComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxloader';
import { jqxNotificationComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxnotification';

@Component({
  selector: 'app-closing-fifo-report',
  templateUrl: './closing-fifo-report.component.html',
  styleUrls: ['./closing-fifo-report.component.scss']
})
export class ClosingFifoReportComponent implements OnInit {

  @ViewChild('quickAccess', { read: ViewContainerRef }) quickAccess: ViewContainerRef;
  @ViewChild('sComboBox') sComboBox: jqxComboBoxComponent;
  @ViewChild('jqxLoader') jqxLoader: jqxLoaderComponent;
  @ViewChild('winNotification') winNotification: jqxNotificationComponent;
  storeAdapter: any;
  store_data: any;
  userData: any = {};
  closingFifoId: any;
  apiUrl: string;
  reportData: any = [];
  showGenerateReportButton: boolean = false;
  showDataNotFoundButton: boolean  = false;

  constructor(
    private componentFactoryResolver: ComponentFactoryResolver,
    private cus: CurrentUserService,
    private rs: AllReportService,
    private fes: FileExportService,
    @Inject('API_URL') apiUrl: string,
  ) {
    this.userData['token'] = this.cus.getTokenData()['token'];
    this.apiUrl = apiUrl;
  }

  ngOnInit() {
    this.createQuickAccess();
    this.store_data =
      {
        datatype: "json",
        contentType: "application/json; charset=utf-8",
        datafields: [
          { name: 'id' },
          { name: 'name' },
        ],
        data: this.userData,
        url: this.apiUrl + 'store?limit=all',
      };
    this.storeAdapter = new jqx.dataAdapter(this.store_data, {
      downloadComplete: (data, status, xhr) => {
        let tData = [];
        let dt = { };
        dt['id'] = '0';
        dt['name'] = 'Select All';
        tData.push(dt);
        for(let i=0; i<data.data.length; i++){
          tData.push(data.data[i]);
        }
        return tData;
      }
    });
  }

  createQuickAccess() {
    const componentFactory = this.componentFactoryResolver.resolveComponentFactory(QuickaccessComponent);
    this.quickAccess.clear();
    const dyynamicComponent = this.quickAccess.createComponent(componentFactory);
  }

  getClosingFifoId($event) {
    this.closingFifoId = $event.args.item.originalItem.id;
  }

  generateReport(generate: boolean) {
    console.log(this.sComboBox.val());
    if (this.sComboBox.val()) {
      if (!generate) {
        this.showGenerateReportButton = true;
      }
      let post: any = [];

      if (this.closingFifoId > 0) {
        post['store_id'] = this.closingFifoId;
      }
      
      post['current_fifo_report'] = true;
      if (generate) {
        post['current_fifo_report_generate'] = true;
      }
      this.jqxLoader.open();

      this.rs.getStoreLedgerReport(post, generate ? true : false).subscribe(
        (result: any) => {
          this.jqxLoader.close();
          if (generate) {
            this.fes.SaveFile(result, 'Current Fifo Report');
          } else {
            this.reportData = [];
            let itemCount = 0;
            let openingCount = 0;
            result.category.forEach(resultCategory => {
              result.items[itemCount].forEach(resultItems => {
                result.opening[openingCount].forEach(openingItems => {
                  let data = {
                    category: resultCategory.name,
                    itemName: resultItems.name,
                    quantity: openingItems.rest_quantity,
                    rate: openingItems.rate,
                    amount: openingItems.total
                  }
                  this.reportData.push(data);
                })
                openingCount++;
              })
              itemCount++;
            })
            this.showDataNotFoundButton = this.reportData.length ? false : true;
          }
        }, error => {
          console.log(error);
          this.jqxLoader.close();
        }
      )
    } else {
      let messageDiv: any = document.getElementById('windowMessage');
      messageDiv.innerText = "Please select an item"
      this.winNotification.open();
      this.jqxLoader.close();
    }
  }
}



